.container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 40px;
    font-size: 12px;
    color: #212529;
    border-bottom: 1px solid silver;
    height: 20px;
}

.container:last-child {
    border:none;
}

.image {
    width: 30px;
}

.name {
    width: 140px;
    font-size: 14px;
    margin-left: 15px;
    font-weight: bold;
    text-align: left;
    color: #333333;
}

.symbol {
    text-align: left;
    width: 100px;
}

.currentPrice {
    text-align: left;
    width: 100px;
}

.greenPriceChange,
.redPriceChange {
    width: 100px;
    text-align: left;
    font-weight: bold;
}


.redPriceChange {
color: rgb(255,38, 38);
}

.greenPriceChange {
    color: rgb(0, 204, 0);
}